let timer = '';
import http from '@/api/http.js'
import {checkPhone,checkPwd,checkCode} from "@/common/common.js"
import QSPopup from '@/components/QS-popup/QS-popup.vue';
export default {
	components:{QSPopup},
	data() {
		return {
			sendMsg:"获取验证码",
			isCanSendCode:true,
			phone:"",
			code:"",
			islogon:0,
			spellid:0,
			isCur:1,
			spread_user_id:0,//推广人id
		};
	},
	onLoad(option) {
		if(option.islogon){
			this.islogon = option.islogon
			this.spellid = option.id
		}
		if(option.spread_user_id){
			this.spread_user_id = option.spread_user_id
		}
		if(option.isCur){
			this.isCur = option.isCur
		}
	},
	methods: {
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref) {
			this.$refs[ref].hide();
		},
		setCodeInterval () {
			//设置验证码重新发送定时器
		    let time = 60;
		    clearInterval(timer);
		    timer = setInterval(() => {
		        time--;
		        this.isCanSendCode=false;
		        this.sendMsg=time + "s"
		        if (time <= 0) {
		            this.isCanSendCode=true;
		            this.sendMsg="重新获取";
		            clearInterval(timer);
		        }
		    }, 1000)
		},
		clearCodeInterval(){
		    clearInterval(timer);
		    this.codemsg="获取验证码";
		},
		sendCode () {
			let data = {
				phone:this.phone
			}
			http('/api/Login/send_code','post',data).then(res=>{
				if(res.data.status == '200'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				console.log(res)
			})
			//发送验证码
		    if (!checkPhone(this.phone)) {
		        return
		    }
		    this.setCodeInterval();
			/*
			* 发送验证码逻辑
			*/
		},
		submit() {
			if(this.phone == '' || this.code ==''){
				uni.showToast({
					title:'不能为空',
					icon:'none'
				})
				return;
			};
			if(!checkPhone(this.phone)){
				uni.showToast({
					title:'请输入正确手机号',
					icon:'none'
				})
				return;
			};
			if(this.code.length!=5){
				uni.showToast({
					title:'验证码错误',
					icon:'none'
				})
				return;
			};
			let data = {
				phone:this.phone,
				code:this.code
			}
			http('/Api/Login/yzCodeRegister','post',data).then(res=>{
				console.log(res)
				if(res.data.status == '200'){
					uni.navigateTo({
						url:"/pages/index/logon/setPassword?phone="+ this.phone +"&islogon="+this.islogon + "&id=" + this.spellid + "&isCur=" + this.isCur+'&spread_user_id=' + this.spread_user_id
					})
					// this.phone=''
					// this.code=''
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
					return;
				}
			})
			
			/*
			* 注册逻辑
			*/
			// this.$store.commit('update',['isLogin',true]);
			
		}
	}
}
